<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
<div style="text-align:right">
  <v-img
    class="mx-2"
    src="../public/asti.png"
    max-height="60"
    max-width="60"
    contain
  ></v-img>

</div>
    </v-app-bar>

  <v-main flex-center>
    <v-row class="justify-center">
        <div id="gg" ref="g" class="mt-2"></div>
    </v-row>
    <v-spacer></v-spacer>
    <v-row class="justify-center mt-4 pt-4">
      <v-col class="d-flex" cols="12" sm="6">
      
      <DatePicker ref="dateValue" @clicked="update()" class="mr-2"> </DatePicker>

      <v-select
        class="mr-2"
        :items="time"
        @change="update()"
        v-model="sel_hour"
        item-text="default_time"
        label="Hour"
        outlined
      ></v-select>

      <v-select
        :items="items"
        v-model="selected"
        @change="select()"
        item-text="default_graph"
        label="M:S"
        outlined
      ></v-select>
      </v-col>
    </v-row>
  </v-main>
  </v-app>
</template>

<script>
import Dygraph from 'dygraphs'
import { mapState } from 'vuex' 
import DatePicker from './components/DatePicker'

let date = new Date()
export default {
  name: 'App',

  components: {
    DatePicker,
  },
  computed: {
    ...mapState([
      'csv',
      'items',
      'item',
      'time',
    ])
  },

  data: () => ({
    g: '',
    selected: '',
    sel_hour: date.getHours(),
    default_graph: '',
    default_time: '',
    file_name: '',
  }),
  created(){
  },
  watch:{
    selected: function sel(){
    },

  },
  mounted:function (){
    this.update()
  },

  methods:{
    select(){
    if(this.selected !== undefined){
      this.filename = '0' + this.sel_hour + ":" + this.selected 
      this.$store.dispatch('selectItem',[this.$refs.dateValue.selected_date,this.filename]);
      setTimeout(()=>{
        console.log(this.csv)
        this.g.updateOptions({'file':this.csv})
      },1000)
    }
    },
    update(){
      this.$store.dispatch('updateData',[this.sel_hour,this.$refs.dateValue.selected_date]);
  setTimeout(()=>{
    if(this.items[0] !== undefined){
      this.selected = this.items[0]
      this.filename = '0' + this.sel_hour + ":" + this.selected 
      this.$store.dispatch('selectItem',[this.$refs.dateValue.selected_date,this.filename]);
      setTimeout(()=>{

        this.g = new Dygraph(this.$refs.g, this.csv, {
          legend: 'always',
          ylabel: "Signal Strength(dbm)",
          xlabel: "Freq(MHz)",
          drawGrid: false,
          axes: {
          y: {
            valueFormatter: function(v) {
              return v + ' dbm';  // controls formatting in the legend/mouseover
            }
          },
          x: {
            valueFormatter: function(v) {
              return v + ' MHz';  // controls formatting in the legend/mouseover
            },
          }
          }
      })
      },1000)
    }
  },2000)

    }
  }
};
</script> 
