  font-size: 36px; text-shadow: gray 2px 2px 2px; 
  }

#gg .dygraph-ylabel { 
  text-align: center;
  font-size: 18px;  
  margin-right: 150px;
  }
#gg .dygraph-label.dygraph-title {
  margin-left: 10% !important;
  color: red;
}
